// @flow

import {
  type DerivedPoint,
  type DerivedKeyPair,
  type ExtendedData
} from '../../types/hd.js'
import { sha512Hmac } from '../utils/hash.js'
import { toPublic } from '../core/privateKey.js'
import { getHDSetting } from '../core/networkInfo.js'
import { privateKeyTweakAdd, publicKeyTweakAdd } from '../utils/secp256k1'

export const HARDENED = 0x80000000
export const MAX_INDEX = 0xffffffff
export const MAX_DEPTH = 0xff
export const TWEAK_OUT_OF_RANGE_ERROR = 'tweak out of range'

export const getTweakPoint = (
  key: string,
  chainCode: string,
  index?: number
): DerivedPoint => {
  if (typeof index === 'number') {
    if (index > MAX_INDEX) throw new Error('Index out of range.')
    key += index.toString(16).padStart(8, '0')
  }
  const hash = sha512Hmac(chainCode, key)

  return {
    point: hash.slice(0, 64),
    chainCode: hash.slice(64, 128)
  }
}

export const deriveKeyPair = async (
  keyPair: $Shape<DerivedKeyPair>,
  index: number
): Promise<DerivedKeyPair> => {
  try {
    const { privateKey = '', publicKey, chainCode: parentCode } = keyPair
    // Default value for the key we're using to get the tweak point
    let key = `00${privateKey}`
    // If the index is non-hardended, set 'key' to be the publicKey if exists
    // Or try to create it from the privateKey
    if (index < HARDENED) {
      key = publicKey || await toPublic(privateKey, true)
    } else if (!privateKey) {
      throw new Error('Cannot derive a hardened index with a public key.')
    }

    const { point, chainCode } = getTweakPoint(key, parentCode, index)

    const result: $Shape<DerivedKeyPair> = { childNumber: index, chainCode }

    if (!privateKey) {
      result.publicKey = await publicKeyTweakAdd(publicKey, point)
    } else {
      result.privateKey = await privateKeyTweakAdd(privateKey, point)
    }

    return result
  } catch (e) {
    if (!e.message.includes(TWEAK_OUT_OF_RANGE_ERROR)) throw e
    return deriveKeyPair(keyPair, index + 1)
  }
}

export const dataToHex = (data: ExtendedData): string => {
  return (
    data.version.toString(16).padStart(8, '0') +
    data.depth.toString(16).padStart(2, '0') +
    data.parentFingerPrint.toString(16).padStart(8, '0') +
    data.childNumber.toString(16).padStart(8, '0') +
    data.chainCode
  )
}

export const dataFromHex = (keyHex: string, network?: string): ExtendedData => {
  // Check the entire hex length
  if (keyHex.length !== 156) throw new Error('Wrong key length')

  // Check that the key prefix matches the network's prefix if given a network
  const version = parseInt(keyHex.slice(0, 8), 16)
  if (network) {
    const setting = getHDSetting(version, network)
    if (!setting) throw new Error('Wrong extended key version for network')
  }
  return {
    version,
    depth: parseInt(keyHex.slice(9, 10), 16),
    parentFingerPrint: parseInt(keyHex.slice(10, 18), 16),
    childNumber: parseInt(keyHex.slice(18, 26), 16),
    chainCode: keyHex.slice(26, 90)
  }
}
