// @flow

import { getHDSetting, getDecoder } from '../core/networkInfo.js'
import {
  type ExtendedPrivateKey,
  type ExtendedPublicKey
} from '../../types/hd.js'
import { getTweakPoint, dataFromHex, dataToHex } from './common.js'
import { toPublic } from '../core/privateKey.js'

export const HARDENED = 0x80000000
export const MAX_INDEX = 0xffffffff
export const MAX_DEPTH = 0xff
export const TWEAK_OUT_OF_RANGE_ERROR = 'tweak out of range'
export const SEED = '426974636f696e2073656564'

export const fromSeed = async (
  seed: string,
  version: number = 0x0488ade4
): Promise<ExtendedPrivateKey & { publicKey: string }> => {
  const { point: privateKey, chainCode } = getTweakPoint(seed, SEED)
  const publicKey = await toPublic(privateKey, true)
  return {
    privateKey,
    publicKey,
    chainCode,
    version,
    childNumber: 0,
    parentFingerPrint: 0,
    depth: 0
  }
}

export const fromHex = (keyHex: string, network?: string): ExtendedPrivateKey => {
  // Get the byte which tells us what type of key we're expecting
  const headerByte = parseInt(keyHex.slice(90, 92), 16)
  if (headerByte !== 0) throw new Error('Wrong private key header')

  return {
    privateKey: keyHex.slice(92, 156),
    ...dataFromHex(keyHex, network)
  }
}

export const toHex = (key: ExtendedPrivateKey): string =>
  dataToHex(key) + `00${key.privateKey}`

export const fromString = (
  hdKey: string,
  network: string = 'main'
): ExtendedPrivateKey => {
  const keyHex = getDecoder(network, hdKey.slice(0, 4)).decode(hdKey)
  return fromHex(keyHex, network)
}
export const toString = (
  hdKey: ExtendedPrivateKey,
  network: string = 'main'
): string => getDecoder(network, hdKey.version).encode(toHex(hdKey))

export const toXPub = async (hdKey: ExtendedPrivateKey, network?: string): Promise<ExtendedPublicKey> => {
  const { privateKey, ...rest } = hdKey
  const { xpub: { prefix } } = getHDSetting(hdKey.version, network)
  let { publicKey } = hdKey
  if (!publicKey) publicKey = await toPublic(privateKey, true)

  const result = {
    ...rest,
    version: prefix,
    publicKey
  }
  return result
}
