// @flow

import { assert } from 'chai'
import { describe, it } from 'mocha'

import * as HDKey from '../../src/hd/hdKey.js'
import * as Private from '../../src/hd/private.js'
import * as Public from '../../src/hd/public.js'
import { createPaths } from '../../src/hd/path.js'
import fixtures from './fixtures/path.json'

const network = 'main'

describe('Testing Bip44 HD Key path derivation', function () {
  fixtures.createPaths.forEach(test => {
    it(`Deriving HD key ${test[0]} from HD Settings ${
      test[3]
    }`, async function () {
      const parentKey = HDKey.fromString(test[0])

      let account = 0
      let coinType = 0
      if (test[1]) {
        account = test[1][0]
        coinType = test[1][1]
      }
      const hdPaths = createPaths(test[2], coinType, account, 'bitcoin')
      const hdKey = await HDKey.fromPaths(parentKey, hdPaths, network)
      console.log('1. hdKey', hdKey)
      let testIndex = 3
      for (const hdPath of hdPaths) {
        const key = HDKey.getKey(hdKey, hdPath.path)
        if (!key) throw new Error('No Key')
        console.log('2. key', key)
        const publicKey = await Private.toXPub(key, network)
        console.log('3. publicKey', publicKey)
        const xpubkey = Public.toString(publicKey, network)
        console.log('38. xpubkey', xpubkey)
        console.log('41. test[testIndex++]', test[testIndex++])
        // const xpubkey = Public.toString(key, network)
        assert.equal(xpubkey, test[testIndex], 'xpub')

        if (key.privateKey) {
          console.log('4. key', key)
          const xprivkey = Private.toString(key)
          assert.equal(xprivkey, test[testIndex++], 'xpriv')
        }
      }
    })
  })
})
